.modal {
  overflow-y: auto;
  overflow-x: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  max-width: 480px;
  max-height: 940px;

  background-color: var(--background-color);
  font-family: Roboto, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: calc(16 / 12);
  color: var(--primary-text-color);
  padding: 48px 20px 40px 20px;

  @include tablet {
    display: flex;
    gap: 68px;
    align-items: stretch;
    max-height: 552px;
    max-width: 704px;
    padding: 40px 72px 40px 36px;
  }

  @include desktop {
    display: flex;
    gap: 16px;
    max-width: 806px;
    max-height: 558px;
    padding: 40px 12px;
  }
}

.modal__close-btn {
  cursor: pointer;
  z-index: 1;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  transition: color 0.25s linear;
  display: flex;
  position: absolute;
  top: 0%;
  right: 1%;
  transform: rotate(45deg);

  // top: 8px;
  // right: 8px;
  // padding: 0;
  // width: 30px;
  // height: 30px;
  // background-color: transparent;
  // border: 1px solid transparent;
  // outline: none;

  &:hover,
  &:focus {
    color: var(--accent-color);
  }
}

.image {
  height: 100%;
  width: 100%;
  border-radius: 5px;
  box-shadow: 2px -3px 20px 1px #645c5cbf;

  @include tablet {
    width: 264px;
    height: 373px;
  }

  @include desktop {
    width: 375px;
    height: 478px;
  }
}

.film__image {
  position: relative;
  margin-right: auto;
  margin-left: auto;
  width: 240px;
  height: 356px;
  border-radius: 5px;
  margin-bottom: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  background-image: url(/src/images/poster-placeholder.png);

  @include tablet {
    margin-bottom: 0;
    margin-left: 0;
    width: 264px;
    height: 373.55px;
  }

  @include desktop {
    width: 396px;
    height: 477.8px;
  }
}

.film__title {
  font-size: 20px;
  line-height: 1.15;
  margin-top: 0;
  margin-bottom: 20px;

  @include desktop {
    font-size: 30px;
    line-height: 1.17;
  }
}

.film__list {
  margin: 0;
  margin-bottom: 20px;
}

.film__item {
  display: flex;
  &:not(:last-child) {
    margin-bottom: 8px;
  }
}

.film__details {
  color: var(--quality-text-color);
  min-width: 108px;
}

.film__info--uper {
  text-transform: uppercase;
  line-height: 1.17;
}

.film__rating--orange {
  padding-left: 10px;
  padding-right: 10px;
  color: var(--header-text-color);
  background-color: var(--accent-color);
  border-radius: 5px;
}

.film__rating--divider {
  margin-right: 3px;
  color: var(--quality-text-color);
}

.film__about-title {
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 10px;
}

.film__about-text {
  overflow-y: auto;
  line-height: 1.67;
  margin-bottom: 20px;
  max-height: 236px;

  @include desktop {
    max-height: 160px;
  }
}

.button-wrapper {
  display: flex;
}

.film__button {
  width: 112px;
  height: 44px;
  text-align: center;
  text-transform: uppercase;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
  border: 1px solid #000000;
  transition: all 300ms cubic-bezier(0.38, 0.53, 0.8, 0.71);

  &:hover {
    background-color: var(--accent-color);
    color: var(--header-text-color);
    border: 1px solid var(--accent-color);
  }

  @include tablet {
    width: 125px;
  }

  @include desktop {
    width: 136px;
  }
}

.film__button.active {
  background-color: var(--accent-color);
  color: var(--header-text-color);
  border: 1px solid var(--accent-color);
}

.film__button:not(:last-child) {
  margin-right: 20px;
  @include tablet {
    margin-right: 15px;
  }
}

.btn__trailer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

