.header {
    height: 230px;
    padding-top: 40px;
    background-color: var(--quality-text-color);

    &__main-bckg {
    background-image: linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)), url(../images/header/mob/header_m_1x.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    @include retina {
    background-image: linear-gradient(rgba(25, 28, 38, 0.2), rgba(25, 28, 38, 0.2)), url(../images/header/mob/header_m_@2x.jpg);
    }

    @include tablet {
    background-image: linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)), url(../images/header/tabl/header_tab_1x.jpg);
    background-repeat: repeat;
    
        @include retina {
            background-image: linear-gradient(rgba(25, 28, 38, 0.2), rgba(25, 28, 38, 0.2)), url(../images/header/tabl/header_tab_@2x.jpg);
        }
    }
    
    
    @include desktop {
            background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                        rgba(47, 48, 58, 0.4)), url(../images/header/desc/header_d_1x.jpg);
            
            
        @include retina {
            background-image: linear-gradient(rgba(25, 28, 38, 0.2),rgba(25, 28, 38, 0.2)), url(../images/header/desc/header_d_@2x.jpg);
        }
    }
    }        
&__container {
    position: relative;    
}

&__logo {
align-items: center;
display: flex;
    &-name {
        display: none;

    @include tablet {
        display: flex;
        font-size: 30px;
        line-height: 1.17;
        color: var(--header-text-color);
        transition: color .25s cubic-bezier(.4, 0, .2, 1);
        }
    }
    &-icon {
    margin-right: 10px;
}
}

&__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-list {
        display: flex;           
    }
    &-link {
        background-color: initial;
        border: none;
        margin: 0;
        padding: 7px 0 3px;
        line-height: 1.17;
        text-transform: uppercase;
        color: #fff;
            &:hover,
            &:focus {
            outline: none;
            color: var(--header-text-color);
            text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #0fa, 0 0 82px #0fa, 0 0 92px #0fa, 0 0 102px #0fa, 0 0 151px #0fa;                    
            }
    }
    
       &-item:not(:last-child) {
        margin-right: 40px;
        }
    
        &-item {
            position: relative;
            &--active::after {
                content: '';
                position: absolute;
                left: 0;
                bottom: 0px;
                width: 100%;
                height: 3px;
                background-color: var(--notification-color);
            }
    
        }
        
        &-btn {
            background-color: initial;
            border: none;
            margin: 0;
            padding: 7px 0 3px;
            line-height: 1.17;
            text-transform: uppercase;
            color: #fff;
    
        }
}

&__searcher {
    position: relative;
    margin-top: 56px;
}

&__form {
    display: flex;
    justify-content: center;
}

&__input {
    background-color: transparent;
    border: none;
    border-bottom: 0.5px solid #fff;
    padding: 0 20px 4px 0;
    color: var(--header-text-color);
    outline: none;
    transition: border-bottom .25s cubic-bezier(.4, 0, .2, 1);
    width: 280px;
    font-weight: 400;
    line-height: 1.14;
    font-size: 14px;
    &::placeholder {
    font-weight: 400px;
    font-size: 14px;
    line-height: 1.14;
    color: var(--header-text-color);
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    }
    @include tablet {
    width: 336px;
}
    @include desktop {
    width: 394px;
}
}
&__search-icon {
    stroke: #fff;
    transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

&__input-btn {
    border: none;
    background-color: inherit;
    transform: translateX(-100%);
    padding: 0;  
}

&__list-item:first-child {
    margin-right: 20px;
    @include tablet {
    margin-right: 32px;    
    }
    @include desktop {
    margin-right: 16px;
    }
}
&__list-btn {
    display: flex;
    justify-content: center;
    justify-items: center;
    margin-top: 0;
}

&__item-btn {
    border: 1px solid #fff;
    color: #fff;
    background-color: initial;
    align-content: center;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    border-radius: 5px;
    font-size: 12px;
    width: 130px;
    height: 44px;
    &--active {
        background-color: #ff6b08;
        border: none;
        filter: drop-shadow(0 8px 43px rgba(255, 107, 1, .6));
        }
    @include tablet {
        width: 152px;
    }
    @include tablet {
        width: 148px;
        height: 42px;
        }
}
}

.current {
    position: relative;
}

.current::after {
    content: '';
    position: absolute;
    opacity: 1;
    transform: scaleX(1);
    display: block;
    width: 100%;
    height: 3px;
    background-color: var(--notification-color);
    }


.icon-up-arrow__container {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: block;
    text-align: center;
    font-size: 1.375em;
    background-color: var(--accent-color);
    fill: var(--header-text-color);
    stroke: var(--header-text-color);
    bottom: 0;
    right: 0;
    z-index: 999;
    position: fixed;
    opacity: 50%;
    padding: 2px;
    margin: 10px;
    cursor: pointer;

    &:focus,
    &:hover {
        opacity: 100%;
        box-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #ff6b08, 0 0 92px #ff6b08, 0 0 102px #ff6b08, 0 0 102px #ff6b08, 0 0 152px #ff6b08;
    }


}

.icon-up-arrow {
    width: 100%;

    fill: #FFFFFF;
    stroke: #FFFFFF;
    margin-bottom: 10px;



}